type Props = {
  goods: Good[],
};

export const GoodsList: React.FC<Props> = ({ goods }) => {
  return (
    <table className="table is-narrow">
      <thead>
        <tr>
          <th>#</th>
          <th>Name</th>
          <th>Color</th>
        </tr>
      </thead>

      <tbody>
        {goods.map(good => (
          <tr
            key={good.id}
          >
            <td
              style={{ color: good.color }}
            >
              {good.id}
            </td>
            <td
              style={{ color: good.color }}
            >
              {good.name}
            </td>
            <td
              style={{ color: good.color }}
            >
              {good.color}
            </td>
          </tr>
        ))}
      </tbody>
    </table>
  );
};
